import { useSphere } from '@react-three/cannon';
import { useFrame, useThree } from '@react-three/fiber';
import { useEffect, useRef } from 'react';
import { usePlayerControls } from '../../shared/utils/controls';
import * as THREE from 'three';
import { PointerLockControls } from '@react-three/drei';

const BaseCharacter = (props: any) => {
  const direction = new THREE.Vector3();
  const frontVector = new THREE.Vector3();
  const sideVector = new THREE.Vector3();
  const speed = new THREE.Vector3();
  const SPEED = useRef(1);
  const control: any = useRef()

  const { camera,gl } = useThree();

  const [ref, api]: any = useSphere((index) => ({
    mass: 67,
    type: "Dynamic",

    position: [0, 10, 0],
    ...props,
  }));

  document.addEventListener("keypress", (e)=>{ 
    if(e.code === "KeyG"){
      if(control.current){
        if(!control.current.isLocked){
          control.current.connect()
          control.current.lock()
        }else{
          control.current.unlock()
        }
      }
    }
  })
  document.addEventListener('click',()=>{
    if(control.current && !control.current.isLocked){
      control.current.disconnect()
      control.current.unlock()
    }
  })

  const { forward, backward, left, right, jump, speedup } = usePlayerControls();
  const velocity: any = useRef([0, 0, 0]);
  useEffect(() => api.velocity.subscribe((v: any) => (velocity.current = v)), []);

  useFrame((state) => {
    SPEED.current = speedup ? 4 : 1
    ref.current.getWorldPosition(camera.position);
    frontVector.set(0, 0, Number(backward) - Number(forward));
    sideVector.set(Number(left) - Number(right), 0, 0);
    direction.subVectors(frontVector, sideVector).normalize().multiplyScalar(SPEED.current).applyEuler(camera.rotation);
    speed.fromArray(velocity.current);
    
    api.velocity.set(direction.x, velocity.current[1], direction.z);
    if (jump && Math.abs(velocity.current[1].toFixed(2)) < 0.05) api.velocity.set(velocity.current[0], 5, velocity.current[2]);
  });

  return (
    <group>
      <PointerLockControls ref={control} args={[camera, gl.domElement]}/>
      <mesh castShadow position={props.position} ref={ref}>
        {/* <sphereGeometry args={props.args} /> */}
        <boxGeometry args={props.args}/>
        <meshStandardMaterial color="#FFFF00" />
      </mesh>
    </group>
  );
};

export default BaseCharacter;
