import { Canvas } from '@react-three/fiber';
import {Physics} from '@react-three/cannon';
import PlayGround from './test'
import './Scene.scss'
import { Suspense, useEffect, useState } from 'react';
import { AWSDControl } from './control';
import  Loader  from './Loading';
function Scene() {
  const [awsd, setAWSD]= useState({
    a: false,
    w: false,
    s: false,
    d: false,
    space: false
  })
  const isProd = process.env.NODE_ENV === "production"
  useEffect(()=>{
    AWSDControl((e)=>{
      setAWSD(state=> ({
        ...state,
        a: e.A,
        w: e.W,
        s: e.S,
        d: e.D,
        space: e.SPACE
      }))
    })

  },[])
  return (
    <>
      {!isProd ?
        <div className='key-show' >
          <strong>KeyBoard</strong>
          <div className='page-wrapp'>        
            <p className={awsd.w ? 'active' : ''}>W</p>
            <p>
              <span className={awsd.a ? 'active' : ''}>A</span>
              <span className={awsd.s ? 'active' : ''}>S</span>
              <span className={awsd.d ? 'active' : ''}>D</span>
            </p>
            <p className={awsd.space ? 'active' : ''}>-SPACE-</p>
          </div>
        </div>
        : null
      }
      <div className='scene-container'>
        <Canvas shadows>
          <Suspense fallback={<Loader />}>
            <Physics 
              gravity={[0, - 9.8 ,0]}
            >
              <PlayGround />
            </Physics>
          </Suspense>
        </Canvas>
      </div>
    </>
  )
}

export default Scene