import { Html, useProgress } from '@react-three/drei'
import "./Loading.scss"
export default function Loader() {
  const { progress } = useProgress()

  return <Html 
    center
    wrapperClass='loading'
  >
    <h1>
      {progress.toFixed(2)} % loaded 
    </h1>
    <hr className='line' style={{
      width: progress + "%",
    }}/>
      
  </Html>
}