import { useState, useEffect } from 'react';

/*****************
 * Player Controls
 ****************/
export interface keys{
  KeyW: string;
  KeyS: string;
  KeyA: string;
  KeyD: string;
  Space: string;
  ShiftLeft: string;
}
export const usePlayerControls = () => {
  const keys: keys = { KeyW: 'forward', KeyS: 'backward', KeyA: 'left', KeyD: 'right', Space: 'jump',ShiftLeft: "speedup" };
  const moveFieldByKey = (key: keyof keys) => keys[key];

  const [movement, setMovement] = useState({ forward: false, backward: false, left: false, right: false, jump: false,speedup: false });

  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => setMovement((m) => ({ ...m, [moveFieldByKey(e.code as keyof keys)]: true }));
    const handleKeyUp = (e: KeyboardEvent) => setMovement((m) => ({ ...m, [moveFieldByKey(e.code as keyof keys)]: false }));

    document.addEventListener('keydown', handleKeyDown);
    document.addEventListener('keyup', handleKeyUp);

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
      document.removeEventListener('keyup', handleKeyUp);
    };
  }, []);
  return movement;
};