export function keyControl(callBack: (data: MouseEvent)=> void){
  document.addEventListener("click",(e)=>{ callBack(e)})
}

interface AWSDController{
  A: boolean
  W: boolean
  D: boolean
  S: boolean
  SPACE: boolean
}
export function AWSDControl(callBack: (data: AWSDController)=> void){
  let controller: AWSDController = {
    A: false,
    W: false,
    D: false,
    S: false,
    SPACE: false
  }
  document.addEventListener("keydown",(e)=>{ 
    if(e.code === "KeyA"){
      controller.A = true
    }
    if(e.code === "KeyW"){
      controller.W = true
    }
    if(e.code === "KeyS"){
      controller.S = true
    }
    if(e.code === "KeyD"){
      controller.D = true
    }
    if(e.code === "Space"){
      controller.SPACE = true
    }
    callBack(controller)
  })
  document.addEventListener("keyup",(e)=>{ 
    if(e.code === "KeyA"){
      controller.A = false
    }
    if(e.code === "KeyW"){
      controller.W = false
    }
    if(e.code === "KeyS"){
      controller.S= false
    }
    if(e.code === "KeyD"){
      controller.D = false
    }
    if(e.code === "Space"){
      controller.SPACE = false
    }
    callBack(controller)
  })
}