import { useLoader } from "@react-three/fiber"
import { useEffect } from "react"
import { LinearEncoding, RepeatWrapping, TextureLoader } from "three"
import { useBox, useSphere } from "@react-three/cannon"

function PlayGround(){
  const [roughness, normal]= useLoader(TextureLoader,[
    process.env.PUBLIC_URL + "texture/terrain-roughness.jpg",
    process.env.PUBLIC_URL + "texture/terrain-normal.jpg",
  ])
  const [ref] = useBox(()=>({
    mass: 0,
    position: [0,0,0],
    type: "Static",
    args: [30,1,30]
  }))
  useEffect(()=>{
    [normal, roughness].forEach((t)=> {
      t.wrapS = RepeatWrapping;
      t.wrapT = RepeatWrapping;
      t.repeat.set(8,8);
    });
    normal.encoding = LinearEncoding
  },[roughness, normal]);
  return (
    <>
      <mesh receiveShadow castShadow ref={ref as any}>
        <boxGeometry  args={[30,1,30]}/>
        {/* <meshStandardMaterial 
          normalMap={normal}
          normalScale={[0.15,0.15] as any}
          roughnessMap={roughness}
          envMapIntensity={0}
          dithering ={true}
          color={[0.015, 0.015, 0.015]}
          roughness={0.7}
         /> */}
         <meshStandardMaterial color={"#efefef"} />
      </mesh>
    </>
  )
}
export default PlayGround